/**
 * ajax
 * @class Ajax
 * @constructor
 * @param {}
 */
class Ajax {
    constructor() {
        this.xhr = new XMLHttpRequest();
        this.xhr.overrideMimeType('application/json;charset=utf-8');
        this.xhr.withCredentials = false;
        this.baseurl = '';
        this.headers = {};
    }
    /**
     * 
     * @method create
     * @for Ajax
     * @param {string} baseurl 
     */
    create(baseurl) {
        this.baseurl = baseurl;
        return this;
    }

    /**
     * 
     * @method setHeader
     * @param {string} key 
     * @param {string} value 
     */
    setHeader(key, value) {
        this.headers[key] = value;
    }

    /**
     * 
     * @method setHeaders
     * @param {object} headers 
     */
    setHeaders(headers) {
        this.headers = Object.assign(this.headers, headers);
    }

    /**
     * GET
     * @method get
     * @param {string} url URL
     * @param {object} params 
     * @return {Promise} Promise
     */
    get(url, params = {}) {
        let requestUrl = `${this.baseurl}${url}`;
        if (Object.keys(params).length > 0) {
            requestUrl += '?';
            for (const key in params) {
                requestUrl += `${key}=${params[key]}&`;
            }
            requestUrl = requestUrl.slice(0, -1);
        }
        this.xhr.open('GET', requestUrl);
        this.setHeaders(this.headers);
        return this.sendRequest();
    }

    /**
     * POST
     * @method post
     * @param {string} url URL
     * @param {object} data 
     * @return {Promise} Promise
     */
    post(url, data = {}) {
        const requestUrl = `${this.baseurl}${url}`;
        this.xhr.open('POST', requestUrl);
        this.setHeaders(this.headers);
        this.xhr.setRequestHeader('Content-Type', 'application/json;charset=utf-8');
        const requestBody = JSON.stringify(data);
        return this.sendRequest(requestBody);
    }

    /**
     * put
     * @method put
     * @param {string} url URL
     * @param {object} data 
     * @return {Promise} Promise
     */
    put(url, data = {}) {
        const requestUrl = `${this.baseurl}${url}`;
        this.xhr.open('PUT', requestUrl);
        this.setHeaders(this.headers);
        this.xhr.setRequestHeader('Content-Type', 'application/json;charset=utf-8');
        const requestBody = JSON.stringify(data);
        return this.sendRequest(requestBody);
    }

    /**
     * delete
     * @method delete
     * @param {string} url URL
     * @param {object} params 
     * @return {Promise} Promise
     */
    delete(url) {
        const requestUrl = `${this.baseurl}${url}`;
        this.xhr.open('DELETE', requestUrl);
        this.setHeaders(this.headers);
        return this.sendRequest();
    }

    /**
     * 
     * @method sendRequest
     * @param {string} requestBody 
     * @return {Promise} Promise
     */
    sendRequest(requestBody) {
        return new Promise((resolve, reject) => {
            this.xhr.onload = () => {
                if (this.xhr.readyState === 4) {
                    if (this.xhr.status === 200) {
                        let responseData;
                        try {
                            responseData = JSON.parse(this.xhr.responseText);
                        } catch {
                            responseData = this.xhr.responseText;
                        }
                        const response = { code: this.xhr.status, data: responseData };
                        resolve(response);
                    } else {
                        reject(new Error(this.xhr.statusText));
                    }
                }
            };

            this.xhr.onerror = () => {
                reject(new Error('Network Error'));
            };

            for (const key in this.headers) {
                this.xhr.setRequestHeader(key, this.headers[key]);
            }

            if (requestBody) {
                this.xhr.send(requestBody);
            } else {
                this.xhr.send();
            }
        });
    }
}

const ajax = new Ajax();

export default ajax;