export const home = async () => {
    // 
    const Lottery = async () => {
        let data = await $post("do", {});
        return data.code//Json{"code":200,"cishu":100}
    };

    ButtonAlert({
        //
        requestFunction: Lottery,//
        value: "Post",
        duration: 2000,
        className: "test"
    });

    ButtonAlert({
        requestFunction: async () => {
            let data = await $get("add", {})//
            return data.cishu;//Json{"code":200,"cishu":100}
        },
        value: "Get",
        duration: 2000,
        className: "test"
    });
    // test({
    //     buttonValue:"",
    //     buttonClick: ()=>{
    //         gorouter('/tea')
    //     },
    // })
};



// <a>
document.addEventListener("click", (event) => {
    if (event.target.tagName === "A") {
        event.preventDefault(); // <a>
        const href = event.target.getAttribute("href");
        gorouter(href)
    }
})


